<template>
<div v-if="status">
    <h4 class="heading">Update Password</h4>
  <div class="password-update-form">
    
    <form @submit.prevent="updatepassword">
      <label for="username">Username</label>
      <input type="text" name="username" placeholder="Enter your registered username" v-model="username" required>
      <br><br> 
      <label for="password">Password</label>
      <input type="password" name="password" placeholder="Set new password" v-model="password" minlength="8" required>
      <br><br>
      <label for="re-enter-password">Re-enter Password</label>
      <input type="password" name="re-enter-password" placeholder="Re-enter new password" v-model="check" minlength="8" required>
      <br><br>
      <button type="submit" v-if="password === check" class="btn btn-primary btn-sm">Update Password</button>
      <p v-else class="error-message">Passwords do not match.</p>
    </form>
  </div>
</div>
</template>

<script>
import { url } from '@/functions/url';
import alertify from 'alertifyjs';
import { update } from '@/functions/update';
import { hashify } from '@/functions/hash';

export default{
    name:'ResetPassword',
    data(){
        return{
            token:null,
            status:false,
            password:'',
            check:'',
            username:''

        }
    },
    methods:{
        async mount(){
        const response = await fetch(`${url}/api/reset/password/${this.token}`)        
            if(response.ok){
                const data = await response.json();
                if(data.message!='Token valid'){
                    alertify.alert('Reset link is invalid/expired')
                    setTimeout(()=>{this.$router.push('/signin')},2500)

                }
                else{
                    this.status=true
                    console.log(data.message)
                }
        }
            else{
                console.log(response.statusText)
                setTimeout(()=>{this.$router.push('/signin')},2500)
            }

        },
        async updatepassword(){
            await update(`${url}api/reset/password`,{token:this.token,password:hashify(this.password),username:this.username},this.$router);
            window.close();
        }



    },
    mounted(){
        this.token = this.$route.params.token;
        this.mount()
        
    }
}
</script>
